#calculator-component {
  color: white;
  position: relative;
  padding: 8em 0;
  text-align: center;
  transition: background, .7s, color .7s; }
  #calculator-component .container {
    display: flex;
    flex-direction: column; }
  #calculator-component .calculator {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%; }
  #calculator-component .inputs {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%; }
  #calculator-component .calculator__input-group {
    max-width: 150px;
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 2em 2em 0 2em; }
  #calculator-component .calculator__input-group input {
    position: relative !important;
    border: 1px solid rgba(255, 255, 255, 0.5);
    width: 80px;
    height: 80px;
    border-radius: 1rem !important;
    color: #fff;
    font-size: 1.5em;
    line-height: 1.5em;
    text-align: center; }
  #calculator-component .calculator__input-group_people input {
    background: url("../img/controls.svg") no-repeat 90% center; }
  #calculator-component .calculator__input-group_liters input {
    background: url("../img/controls.svg") no-repeat 90% center; }
  #calculator-component .calculator__input-group_price:after {
    content: '₽';
    position: absolute;
    bottom: 45px;
    right: 5px;
    font-size: 13px; }
  #calculator-component .calculator, #calculator-component .calculator-description {
    width: 100%;
    max-width: 600px;
    margin: 0 auto; }
  #calculator-component .result {
    margin-top: 2em; }
  #calculator-component .calculator__result {
    margin: 1em 0;
    font-size: 1.8rem;
    line-height: 2rem;
    font-weight: 800; }
  #calculator-component .calculator__result p {
    max-width: 200px; }
  #calculator-component:after {
    content: '';
    display: block;
    position: absolute;
    width: 150vw;
    height: 150vw;
    top: 45%;
    left: 50%;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 100%;
    transform: translate(-50%, -50%);
    pointer-events: none; }
  #calculator-component:before {
    content: '';
    display: block;
    position: absolute;
    width: 250vw;
    height: 250vw;
    top: 45%;
    left: 50%;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 100%;
    transform: translate(-50%, -50%);
    pointer-events: none; }
